@font-face {
  font-family: "Exo2-Thin";
  src: local("Exo2-Thin"), url("../fonts/Exo2-Thin.ttf") format("ttf");
  font-style: normal;
  font-weight: 100;
  font-display: swap; }

@font-face {
  font-family: "TrebuchetMS";
  src: local("TrebuchetMS"), url("../fonts/TrebuchetMS.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "TrebuchetMS-Bold";
  src: local("TrebuchetMS-Bold"), url("../fonts/TrebuchetMS-Bold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

body {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  height: auto;
  font-family: "Trebuchet MS", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  text-align: left;
  color: #000000;
  background-image: url("../img/black-pattern.png");
  background-position: center;
  background-repeat: repeat;
  background-color: #000000;
  background-size: cover; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto; }

.product {
  min-width: 320px;
  margin: 37px auto; }

.product__title {
  margin: 0 auto;
  padding-bottom: 25px;
  font-family: "Exo 2", sans-serif;
  font-size: 36px;
  font-weight: 100;
  text-align: center;
  color: #f2f2f2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: subpixel-antialiased; }

.product__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center; }

.product__item {
  position: relative;
  margin-bottom: 10px; }

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .product__item::after {
    content: "";
    position: absolute;
    top: 0;
    left: -1px;
    border: 21px solid transparent;
    border-left: 21px solid #191919;
    border-top: 22px solid #191919; } }

.product__card {
  width: 320px;
  max-height: 480px;
  background-color: #f2f2f2;
  clip-path: polygon(13% 0%, 100% 0%, 100% 100%, 0 100%, 0 9%);
  border: 5px solid #1698d9;
  border-radius: 15px;
  cursor: pointer; }
  .product__card:hover, .product__card:focus {
    border: 5px solid #2ea8e6;
    transition-delay: 0.3s; }
  .product__card:hover .product__weight,
  .product__card:focus .product__weight {
    background-color: #2ea8e6;
    transition-delay: 0.3s; }
  .product__card:hover::before, .product__card:focus::before {
    background-color: #2ea8e6;
    transition-delay: 0.3s; }
  .product__card--active {
    border: 5px solid #d91667; }
    .product__card--active:hover, .product__card--active:focus {
      border: 5px solid #e62e7a;
      transition-delay: 0.3s; }
    .product__card--active:hover .product__weight,
    .product__card--active:focus .product__weight {
      background-color: #e62e7a;
      transition-delay: 0.3s; }
    .product__card--active:hover::before, .product__card--active:focus::before {
      background-color: #e62e7a;
      transition-delay: 0.3s; }
  .product__card--active .product__weight {
    background-color: #d91667; }
  .product__card--disabled {
    border: 5px solid #b3b3b3; }
  .product__card--disabled .product__weight {
    background-color: #b3b3b3; }
  .product__card--disabled .product__box {
    opacity: 0.5; }
  .product__card--disabled .product__img {
    opacity: 0.5; }

.product__card::before {
  content: "";
  position: absolute;
  top: 21px;
  left: -8px;
  width: 59px;
  height: 5px;
  background-color: #1698d9;
  -webkit-transform: rotate(90deg);
  transform: rotate(134deg);
  z-index: 1; }

.product__card--active::before {
  background-color: #d91667; }

.product__card--disabled::before {
  background-color: #b3b3b3; }

.product__box {
  padding: 17px 58px 307px 43px; }

.product__text {
  margin: 0;
  padding-bottom: 5px;
  color: #666666; }

.product__subtitle {
  margin: 0;
  font-size: 48px;
  font-weight: bold; }

.product__taste {
  margin: 0;
  padding-bottom: 14px;
  font-size: 24px;
  font-weight: bold; }

.product__description {
  margin: 0;
  max-width: 126px;
  font-size: 14px;
  line-height: 16px;
  color: #666666; }

.product__img {
  position: absolute;
  left: 5px;
  bottom: 49px;
  width: 97%;
  height: 91%;
  background-image: url("../img/cat.png");
  background-position: center 208px;
  background-repeat: no-repeat;
  border-radius: 10px; }

.product__weight {
  position: absolute;
  right: 15px;
  bottom: 42px;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  background-color: #1698d9;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center; }

.product__number {
  margin: 0;
  padding-top: 20px;
  padding-bottom: 8px;
  font-size: 43px;
  line-height: 22px;
  color: #f2f2f2; }

.product__kilo {
  font-size: 22px;
  font-weight: 500;
  line-height: 22px;
  color: #f2f2f2; }

.product__offer {
  padding-top: 2px;
  font-size: 13px;
  line-height: 16px;
  color: #f2f2f2;
  text-align: center; }

.product__offer-mark {
  margin-left: 3px;
  font-weight: bold;
  cursor: pointer;
  color: #1698d9;
  border-bottom: 1px dashed #1698d9; }

.product__offer-mark::before {
  content: ".";
  position: absolute;
  right: 53px;
  color: #1698d9; }

@media (min-width: 768px) {
  .product__list {
    flex-direction: row;
    justify-content: space-around; }
  .product__item--left {
    order: 0; }
  .product__item--right {
    order: 1; }
  .product__item--center {
    order: 2;
    margin: 0 160px; } }

@media (min-width: 1280px) {
  .product__item {
    margin: 0; }
    .product__item--left {
      margin-left: 40px; }
    .product__item--right {
      order: 2;
      margin-right: 40px; } }
